:root {
  --background-color: #636480;
  --gastly-background-color: #3a3434;
  --gastly-shine-color: #635759;
  --gastly-border-color: #3b343b;
  --gastly-color-white: white;
  --gastly-eye-shadow-color: #d6d6d6;
  --gastly-eye-color: #342e30;
  --gastly-brow-color: #9f929b;
  --gastly-mouth-color: #bfa4b3;
  --gastly-shape-color: #a0879d;
  --gastly-size: 225px;
  --gastly-shape: polygon(
    65% 0,
    90% 20%,
    100% 45%,
    90% 80%,
    65% 100%,
    35% 100%,
    10% 80%,
    0 45%,
    10% 20%,
    35% 0
  );
  --gastly-inner-shape: polygon(
    62% 0,
    97% 20%,
    100% 45%,
    90% 80%,
    65% 100%,
    35% 100%,
    10% 80%,
    0 45%,
    3% 20%,
    38% 0
  );
  --eye-shape: polygon(
    0 38%,
    16% 0%,
    50% 40%,
    90% 57%,
    81% 88%,
    54% 100%,
    24% 90%
  );
  --eye-inner-shape: polygon(
    0 46%,
    16% 0,
    50% 40%,
    93% 60%,
    81% 88%,
    54% 100%,
    24% 90%
  );
  --brow-shape: polygon(0 0, 100% 60%, 50% 100%, 0 100%, 50% 60%);
  --mouth-shape: polygon(
    0 0,
    30% 39%,
    70% 39%,
    100% 0,
    100% 50%,
    80% 85%,
    50% 100%,
    20% 85%,
    0 50%
  );
  --fang-shape: polygon(0 0, 50% 50%, 100% 0);
  --custom-border: drop-shadow(0 -5px var(--gastly-border-color))
    drop-shadow(0 5px 0 var(--gastly-border-color))
    drop-shadow(5px 0 0 var(--gastly-border-color))
    drop-shadow(-5px 0 var(--gastly-border-color));
}
html,
body,
.container {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}
.perspective-container {
  persperctive: 500px;
}
.pokemon-container {
  position: relative;
  z-index: 1;
  width: var(--gastly-size);
  height: var(--gastly-size);
  transform: translate(-20px, 30px);
  animation: gastlyFly 3s infinite alternate;
}
.pokemon-container .gastly-container {
  width: 100%;
  height: 100%;
  filter: var(--custom-border);
}
.pokemon-container .gastly-container .gastly {
  background: var(--gastly-shine-color);
  width: 100%;
  height: 100%;
  overflow: hidden;
  clip-path: var(--gastly-shape);
}
.pokemon-container .gastly-container .gastly .gastly-shadow {
  background: var(--gastly-background-color);
  width: 100%;
  height: 100%;
  overflow: hidden;
  transform: translateY(15px) scale(1);
  clip-path: var(--gastly-inner-shape);
}
.pokemon-container .eyes-container {
  position: absolute;
  z-index: 5;
  transform: translate(-10px, 55px);
  display: flex;
  filter: var(--custom-border);
}
.pokemon-container .eyes-container .eye-container {
  width: 123px;
  height: 123px;
  background: var(--gastly-eye-shadow-color);
  clip-path: var(--eye-shape);
}
.pokemon-container .eyes-container .eye-container.right {
  transform: scaleX(-1);
}
.pokemon-container .eyes-container .eye-container .eye-inner {
  background: var(--gastly-color-white);
  width: 100%;
  height: 100%;
  overflow: hidden;
  clip-path: var(--eye-inner-shape);
  transform: translateY(-13px);
}
.pokemon-container .eyes-container .eye-container .eye-inner:after {
  position: absolute;
  content: "";
  display: block;
  width: 6px;
  height: 15px;
  background: var(--gastly-eye-color);
  transform: translate(83px, 85px);
}
.pokemon-container .brows-container {
  position: absolute;
  z-index: 10;
  transform: translate(95px, 100px);
  display: flex;
  justify-content: space-around;
  width: 36px;
}
.pokemon-container .brows-container .brow {
  width: 5px;
  height: 25px;
  background: var(--gastly-brow-color);
  clip-path: var(--brow-shape);
}
.pokemon-container .brows-container .brow.right {
  transform: scaleX(-1);
}
.pokemon-container .mouth-container {
  position: absolute;
  display: flex;
  justify-content: center;
  transform: translate(75px, 165px);
}
.pokemon-container .mouth-container .mouth {
  width: 75px;
  height: 25px;
  background: var(--gastly-mouth-color);
  clip-path: var(--mouth-shape);
}
.pokemon-container .fang-container {
  position: absolute;
  width: 75px;
  display: flex;
  justify-content: space-around;
  transform: translate(75px, 165px);
}
.pokemon-container .fang-container:before,
.pokemon-container .fang-container:after {
  position: absolute;
  content: "";
  display: block;
  width: 15px;
  height: 35px;
  background: var(--gastly-eye-color);
  clip-path: var(--fang-shape);
  transform: translate(-32px, 1px) rotate(18deg);
}
.pokemon-container .fang-container:after {
  transform: translate(32px, 1px) rotate(-18deg);
  z-index: -1;
}
.pokemon-container .fang-container .fang {
  width: 15px;
  height: 35px;
  background: var(--gastly-color-white);
  clip-path: var(--fang-shape);
  transform: translate(-18px, 1px) rotate(18deg);
}
.pokemon-container .fang-container .fang.right {
  transform: translate(18px, 1px) scaleX(-1) rotate(18deg);
}
.pokemon-container .fang-container .fang:before {
  position: absolute;
  content: "";
  display: block;
  width: 15px;
  height: 35px;
  background: var(--gastly-eye-color);
  transform: translate(-18px, 1px) rotate(18deg);
}
.gastly-shape {
  --shape-color: url(#grandient);
  position: absolute;
  z-index: 0;
  width: 400px;
  height: 400px;
  filter: drop-shadow(4px 0 0 var(--gastly-border-color))
    drop-shadow(0 4px 0 var(--gastly-border-color))
    drop-shadow(-4px 0 0 var(--gastly-border-color))
    drop-shadow(0 -4px 0 var(--gastly-border-color))
    drop-shadow(8px 8px 0 #454662);
  animation: shapeFly 2s infinite alternate ease-in-out;
}
.gastly-shape path {
  fill: var(--shape-color);
}
.gastly-shape .shape-2 {
  transform: translateY(4px) scaleY(0.8);
  --shape-color: #a0879d;
  --gastly-border-color: transparent;
}
.background {
  position: absolute;
  z-index: -5;
  background-color: var(--background-color);
  background-image: radial-gradient(circle, #838ea2 40%, #514a6b 70%);
  width: 100vw;
  height: 100vh;
}
.background .polygon {
  position: absolute;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.15);
  clip-path: polygon(25% 65%, 75% 35%, 100% 50%, 100% 100%, 30% 100%);
}
.background .triangle {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.1);
}
.background .triangle-1 {
  clip-path: polygon(0 80%, 25% 100%, 0 100%);
}
.background .triangle-2 {
  clip-path: polygon(0 100%, 40% 80%, 60% 100%);
}
.background .triangle-3 {
  clip-path: polygon(0 100%, 100% 84%, 100% 100%);
}
.background .triangle-4 {
  clip-path: polygon(75% 100%, 100% 25%, 100% 100%);
}
@keyframes gastlyFly {
  0% {
    transform: translate(-15px, 10px);
  }
  to {
    transform: translate(-15px, 20px);
  }
}
@keyframes shapeFly {
  0% {
    transform: translate(-1px, -8px) rotateX(-10deg);
    opacity: 0.9;
  }
  50% {
    opacity: 0.75;
  }
  to {
    transform: translate(-1px, 8px) rotateX(10deg);
    opacity: 0.9;
  }
}
